import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

class CountdownTimer extends React.Component {
  static propTypes = {
    startDate: PropTypes.instanceOf(Date).isRequired,
    targetDate: PropTypes.instanceOf(Date).isRequired,
  }

  state = {
    remainingTime: 0,
  }

  intervalId = null
  dayDuration = 0

  calculateRemainingTime = () => -1 * moment().diff(this.props.targetDate)

  addLeadingZero = (value) => {
    if (value < 10) {
      return `0${value.toString()}`;
    }
    return value;
  }

  tick = () => {
    const durationToStart = moment.duration(moment().diff(this.props.startDate));
    const durationToStartMs = durationToStart.asMilliseconds();

    if (durationToStartMs < 0) return;

    const newState = { ...this.state };
    newState.remainingTime = this.calculateRemainingTime();

    this.setState(newState);

    if (newState.remainingTime <= 0) {
      clearInterval(this.intervalId);
    }
  }

  componentWillMount() {
    const dayDuration = moment(this.props.targetDate).diff(moment(this.props.startDate), 'days');
    this.dayDuration = dayDuration;

    this.intervalId = setInterval(() => {
      this.tick();
    }, 1000);

    this.tick();
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  render() {
    const { remainingTime } = this.state;
    let days = Math.floor((remainingTime / (1000 * 60 * 60 * 24)));
    let hours = moment.duration(remainingTime).get('hours');
    let minutes = moment.duration(remainingTime).get('minutes');
    let seconds = moment.duration(remainingTime).get('seconds');

    days = this.addLeadingZero(days);
    hours = this.addLeadingZero(hours);
    minutes = this.addLeadingZero(minutes);
    seconds = this.addLeadingZero(seconds);

    return (
      <p>
        {this.dayDuration} ngày | {days} ngày - {hours} : {minutes} : {seconds}
      </p>
    );
  }
}

export default CountdownTimer;



