import React, { Component } from 'react';
import CountdownTimer from './CountdownTimer';

class App extends Component {
  render() {
    return (
      <div>
        <CountdownTimer
          startDate={new Date('2017-11-08T00:00+07:00')}
          targetDate={new Date('2018-02-15T00:00+07:00')}
        />
        <CountdownTimer
          startDate={new Date('2018-02-16T00:00+07:00')}
          targetDate={new Date('2018-08-16T00:00+07:00')}
        />
      </div>
    );
  }
}

export default App;



